<template>
  <div class="hello">
    <nav class="navbar navbar-expand-lg navbar-dark bg-dark">
      <a @click='ref123'>
      <router-link  class="navbar-brand" :to="{name: 'home'}">Kino</router-link>
    </a>
      <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
        <span  class="navbar-toggler-icon"></span>
      </button>
      <div class="collapse navbar-collapse" id="navbarSupportedContent">
        <ul class="navbar-nav mr-auto">
          <li @click='ref123' class="nav-item active">
            <router-link  class="nav-link" to="/">Главная</router-link>
          </li>
          <router-link  :to="{name: 'Top'}">
          <li class="nav-item">
            <a class="nav-link" href="#">Рекомендации</a>
          </li>
          </router-link>
        </ul>
        <form class="form-inline my-2 my-lg-0">
          <input v-model="title" class="form-control mr-sm-2" type="search" placeholder="Поиск" aria-label="Search">
          <button @click='search' class="btn btn-outline-success my-2 my-sm-0" type="submit">Найти</button>
        </form>
      </div>
    </nav>
    <div v-if='results' class="result">
    <h2>Результаты поиска:</h2>
    <div class="card-deck">
      <div class="col-sm-3" v-for='movie in results' v-bind:key='movie.id'>
        <router-link :to='{name: "Film", params: { id: movie.id}}'>
            <div class="card">
                <img class="card-img-top img-fluid" :src="movie.image" alt="Card image cap">
            </div>
        </router-link>
        </div>
    </div>
    <div class="mar container">
      <button @click='hide' type="button" class="btn btn-primary btn-lg btn-block">Убрать</button>
    </div>
  </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'Navbar',
  props: {
    msg: String
  },
  data () {
    return {
      title: '',
      url: 'http://127.0.0.1:8000/api/movies/?search=',
      results: null
    }
  },
  methods: {
    search () {
      this.title = this.title[0].toUpperCase() + this.title.slice(1)
      console.log(this.title)
      this.url = 'http://127.0.0.1:8000/api/movies/?search=' + this.title
      axios
        .get(this.url)
        .then(response => (this.results = response.data.results))
      console.log(this.url)
    },
    hide () {
      this.results = null
    },
    ref123 () {
      document.location.reload(true)
    },
    upper (str) {
      if (!str) return str
      return str[0].toUpperCase() + str.slice(1)
    }
  }
}
</script>

